import request from '../index.js'

let http = request.http

let http1 = request.http1

// -
// topicId	true	number	null	id
export const get_chapter_list_by_topic = ({
	topicId,
	pageNum,
	pageSize,
	listOrder,
	loginId
}) => {
	return http.get('/chapter/get_chapter_list_by_topic', {
		params: {
			topicId,
			pageNum,
			pageSize,
			listOrder,
			loginId
		}
	})
}


